.swiper-topLayer {
  width: 100%;
  top: 15%;
  z-index: 1;
  position: absolute;
}

.swiper-bottomLayer {
  width: 100%;
  top: 15%;
  z-index: 0;
  position: absolute;
}

.empty {
  opacity: 0;
}

.swiper-fade-left {
  animation:swiper-left 0.8s 1 normal forwards;
/* Firefox: */
  -moz-animation:swiper-left 0.8s 1 normal forwards;
/* Safari and Chrome: */
  -webkit-animation:swiper-left 0.8s 1 normal forwards;
/* Opera: */
  -o-animation:swiper-left 0.8s 1 normal forwards;
}

.swiper-fade-right {
  animation:swiper-right 0.8s 1 normal forwards;
/* Firefox: */
  -moz-animation:swiper-right 0.8s 1 normal forwards;
/* Safari and Chrome: */
  -webkit-animation:swiper-right 0.8s 1 normal forwards;
/* Opera: */
  -o-animation:swiper-right 0.8s 1 normal forwards;
}

@keyframes swiper-left
{
  0%   {opacity: 1; left:0px; top: 15%;}
  100% {opacity: 0;; left:-20%; top: 15%;}
}

@-webkit-keyframes swiper-left /* Safari and Chrome */
{
  0%   {opacity: 1; left:0px; top: 15%;}
  100% {opacity: 0;; left:-20%; top: 15%;}
}

@keyframes swiper-right
{
  0%   {opacity: 1; left:0px; top: 15%;}
  100% {opacity: 0;; left:20%; top: 15%;}
}

@-webkit-keyframes swiper-right /* Safari and Chrome */
{
  0%   {opacity: 1; left:0px; top: 15%;}
  100% {opacity: 0;; left:20%; top: 15%;}
}


