.color-picker-vertical-slider-wrapper{
  padding: .5em;
}

input.color-picker-vertical-slider[type='range']{
  background-image: url("https://res.cloudinary.com/the-wardrobe/image/upload/v1541706654/Style_and_Fit_Slider.png");
}






input.vertical-slider-red-thumb[type='range']::-webkit-slider-thumb{
